*{
  text-align: center;
}
.app {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 10rem;
}

.app .count {
  margin-bottom: 2rem;
  text-align: center;
  display: flex;
  margin-left: 10rem;
}
.app .count h3 {
  line-height: 8.6rem;
  margin-right: 1rem;
}
.app .count h1 {
  font-size:5em;
  margin-top: 2rem;
}
.app .buttons button {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  border: none;
  margin: 0 2rem;
  font-size: 5em;
  box-shadow: 0 4px 32px 0 rgba(10,14,29,.02), 0 8px 64px 0 rgba(10,14,29,.08);
  cursor: pointer;
}
.app .buttons button:hover{
  font-size: 7em;
}
.app .buttons button:focus{
  outline: 0;
}